<template>
  <section class="links">
    <div class="title">链接编辑</div>
    <table>
      <tbody>
      <tr>
        <th>链接名称</th>
        <th>链接地址</th>
      </tr>
      <tr v-for="(link,index) in links">
        <td>
          <i class="fa fa-plus-circle"
             @click="ADD_NEW_LINK(index)"
             v-if="links.length<4">
          </i>
          <i class="fa fa-minus-circle"
             @click="REMOVE_LINK(index)"
             v-if="links.length>1">
          </i>
          <input type="text"
                 :value="link.name"
                 placeholder="链接名称"
                 spellcheck="false"
                 @input="UPDATE_LINK_NAME({name: $event.target.value, index})">
        </td>
        <td>
          <input type="text"
                 :value="link.href"
                 placeholder="链接地址"
                 spellcheck="false"
                 @input="UPDATE_LINK_HREF({href: $event.target.value, index})">
        </td>
      </tr>
      </tbody>
    </table>
    <div class="panel">
      <button @click="saveLinks">保存</button>
    </div>
  </section>
</template>

<script>
  import {mapMutations, mapActions, mapState} from 'vuex'
  export default{
    created(){
      this.getLinks()
    },
    computed: mapState(['links']),
    methods: {
      ...mapActions(['saveLinks', 'getLinks']),
      ...mapMutations(['UPDATE_LINK_NAME', 'UPDATE_LINK_HREF', 'ADD_NEW_LINK', 'REMOVE_LINK'])
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../style/mixins.scss";

  section.links {
    //left: 200px;
    position:absolute;
    left:25%;
    height: 100%;
    width: calc(60% - 0px);
    font-size:2vw;
    //min-width: 400px;
    .title {
      @include title();
    }
    table {
      //@include center();
      width:100%;
      input {
        box-sizing: border-box;
        height: 30px;
        padding: 6px;
        transition: all 0.2s;
        border-left: 0 solid $green1;
        font-size:2vw;
      }
      input:focus {
        border-left: 6px solid $green1;
      }
      tr {
        height: 50px;
        th {
          &:first-of-type {
            //width: 120px;
            width:10%;
            text-align:right;
          }
          &:last-of-type {
            width: 20%;
          }
        }
        td {
          text-align: center;
          input {
            width: 100%;
          }
          &:first-of-type {
            input {
              max-width: 60px;
            }
            i {
              cursor: pointer;
              transition: all 0.4s;
              &:after {
                content: '';
                width: 10px;
                display: inline-block;
              }
              &:hover {
                color: $green1
              }
            }
          }
        }
      }
    }
    .panel {
      position: absolute;
      //bottom: 95px;//
      bottom:20%;
      right: calc(30%);//
      button {
        @include greenButton();
        //height: 30px;
        height:125%;
        width:150%;
        //width: 80px;
      }
    }
    @media screen and (max-width: 500px) {
		.panel {
			bottom:35%;
		}
		.title {
			//position:absolute;
			width:25%;
			left:30%;
			top:15%;
			//text-align:left;
		}
		table {
			//left:20%;
			//position:static;
			//left:30%;
			//top:25%;
		}
    }
    @media screen and (max-width: 500px) and (-webkit-min-device-pixel-ratio:5){/* 兼容iphone4/4s */
		table {
			left:20%;
		}
	}
  }
</style>



