<template>
  <div class="container">
    <status-bar></status-bar>
    <aside class="console">
      <div class="circle">
        <div class="Qcontainer">
			<div class="film">
				<div class="face front"><img src="../../assets/img/me.jpg" alt="me"></div>
				<div class="face back"><h5>WANG</h5></div>
			</div>
		</div>
      </div>
      <menu class="navigation">
        <ul>
          <router-link :to="{path:'/archive'}" tag="li">
            <i class="fa fa-star fa-fw"></i>
            <span>总览</span>
          </router-link>
          <router-link :to="{name:'articles'}" tag="li">
            <i class="fa fa-file-text fa-fw"></i>
            <span>文章</span>
          </router-link>
          <router-link :to="{name:'links'}" tag="li">
            <i class="fa fa-list-ul fa-fw"></i>
            <span>链接</span>
          </router-link>
          <router-link :to="{name:'account'}" tag="li">
            <i class="fa fa-user fa-fw"></i>
            <span>账户</span>
          </router-link>
          
        </ul>
      </menu>
    </aside>
    <div class="subView">
      <router-view></router-view>
      <div class="fff">
		<aside class="wp_side">
			<section role="complementary">
				<div class="sideBlock unSung">
					<h1>上海交大</h1>
					<a href="#"><img src="../../assets/img/高楼大厦_标准.jpg" alt="Midnight Run" /><span class="moreInfo">饮水</span></a>
					<a href="#"><img class="sideImage" src="../../assets/img/小孩看天_标准.jpg" alt="Wyatt Earp" /><span class="moreInfo">思源</span></a>
				</div>
		</section>
		<section role="complementary">
				<div class="sideBlock overHyped">
					<h1>华中科技</h1>
					<a href="#"><img src="../../assets/img/手握电灯_标准.png" alt="Moulin Rouge" /><span class="moreInfo">明德</span></a>
					<a href="#"><img class="sideImage" src="../../assets/img/商务人士_标准.jpg" alt="King Kong" /><span class="moreInfo">厚学</span></a>
				</div>
			  
			</section>
		</aside>
      </div>
    </div>
  </div>
</template>
<script>
  import {get}        from '../../assets/js/cookieUtil'
  import StatusBar    from './StatusBar.vue'
  import {mapState, mapMutations}   from 'vuex'

  export default{
    created(){
      const user = get('user')
      if (!user) this.$router.push('/')
      if (user && !this.user.name) this.SET_USER({name: user, pwd: ''})
    },
    components: {StatusBar},
    computed: mapState(['user']),
    methods: mapMutations(['SET_USER'])
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../style/wp_css.css";
  $pictureSize: 100px;
  //$pictureSize: 20%;
  .container {
    height: 100%;
  }
  .fff{
	  position: absolute;
	  left: calc(80% + 0px);
	  top: 50px;
	  bottom: 0;
	  width: calc(20% - 0px);
	  height: calc(100% - 50px);
  }
  .fff h1{
	  font-size:4vw;
  }

  aside.console {
    box-sizing: border-box;
    //padding-top: 50px;
    float: left;
    height: calc(100% - 50px);
    //width: 200px;
    width:20%;
    //min-width:90px;
    background-color: transparent;
    .circle {
      width: $pictureSize;
      height: $pictureSize;
      margin: auto;
      border-radius: 50%;
      overflow: hidden;
      img {
        width: $pictureSize;
        height: $pictureSize;
      }
    }
	.Qcontainer {
		height: 100%;
		width: 28%;
		position: relative;
		-webkit-perspective: 800;
		float: left;
		margin-right: 2%;
	}
	.film {
		width: 100%;
		height: 15em;
		-webkit-transform-style: preserve-3d;
		-webkit-transition: 1s;
	}
	.Qcontainer:hover .film {
		-webkit-transform: rotateY(180deg);
	}
	.face {
		position: absolute;
		-webkit-backface-visibility: hidden;
	}
	.back {
		width: 66%;
		height: 127%;
		-webkit-transform: rotateY(180deg);

		padding: 15%;
	}
    menu {
      //margin: 20px 0;
      margin: 2% 0;
      padding: 0;
      ul {
        //padding: 0 50px;
        padding: 0 2%;
        > li {
          transition: all 0.4s;
          margin-top: 10px;
          padding-left: 4px;
          text-align: center;
          cursor: pointer;
          height: 40px;
          line-height: 40px;
          > span {
            display: inline-block;
            //margin-left: 20px;
            margin-left: 1%;
            font-size: 2.4vw;
          }
          &:hover {
            padding-left: 8px;
          }
        }
      }
    }
  }

  div.subView {
    //margin-left: 200px;
    margin-left:auto;
    height: calc(100% - 50px);
  }

</style>



