<template>
  <section class="account">
    <div class="title">修改密码</div>
    <div class="password">
      <i class="fa fa-key fa-fw"></i>
      <input type="password"
             placeholder="输入新密码"
             v-model="pwd">
      <br>
      <i class="fa fa-key fa-fw"></i>
      <input type="password"
             placeholder="再输入一次"
             v-model="pwdRepeat">
    </div>
    <div class="panel">
      <button @click="savePwd">保存</button>
    </div>
  </section>
</template>
<script>
  export default{
    data(){
      return {
        pwd: '',
        pwdRepeat: ''
      }
    },
    methods: {
      savePwd(){
        if (this.pwd === this.pwdRepeat) {
          this.$store.dispatch('savePwd', this.pwd)
        }
      }
    }
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../style/mixins.scss";

  section.account {
    width: calc(80% - 200px);
    position:absolute;
    height: 100%;
    left:25%;
    .title {
      @include title();
      //left:25%;
      text-align:left;
    }
    .password {
      //width: 260px;
      //position:absolute;
      //top:55%;
      //width:60%;
      //left:45%;
      //margin: 100px auto 40px auto;
      input {
        //height: 30px;
        height:25%;
        width:65%;
        padding: 6px;
        margin: 10px auto;
        font-size:2vw;
        border-bottom: 1px solid $green1;
      }
    }
    .panel {
      position: absolute;
      //bottom: 40px;
      //right: 100px;
      bottom: 40%;//
      right: calc(20%);//
      button {
        @include greenButton();
        height: 30px;
        width: 80px;
      }
    }
    @media screen and (max-width: 500px) {
		.password {
			position:absolute;
			top:25%;
			left:15%;
			width:155%;
		}
		.title {
			position:absolute;
			top:15%;
			left:25%;
		}
		.panel {
			bottom:55%;
			right:-25%;
		}
		.panel button {
			//position:absolute;
			height:5%;
			width:125%;
			//right:calc(160%);
			//right: calc(20%);
		}
    }
  }
</style>



