<template>
  <div class="articles">
    <router-link :to="{name:'editor'}"
                 tag="button">新增文章
    </router-link>
    <table>
      <tbody>
      <tr class="blink">
        <th>标题</th>
        <th>日期</th>
        <th @click="sort_a">选项</th>
      </tr>
      <tr v-for="(article,index) in articles">
        <router-link :to="{name:'article',query:{id:article._id}}"
                     tag="td">
          {{article.title}}
        </router-link>
        <td>
          {{article.date | toDate}}
        </td>
        <td>
          <router-link class="fa fa-pencil-square-o"
                       :to="{name:'editor',query:{id:article._id}}"
                       tag="i">
          </router-link>
          <i class="fa fa-trash"
             @click="deleteArticle(article._id, article.user)">
          </i>
        </td>
      </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
  import {mapState, mapActions} from 'vuex'
  export default{
    created(){
      this.getArticles(this.$store.state.user.name)
    },
    computed: mapState(['articles']),   
    methods: mapActions(['getArticles', 'deleteArticle','sort_9'])
  };
  //function sort_9(tt) {
	//mapState(['articles']).sort((a1,a2)=>(a1.date-a2.date));
	//console.log('wang');
	//return tt
  //}

</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../style/mixins.scss";

  div.articles {
    @include rightSide();
    button {
      @include center();
      position: relative;
      //width: 200px;
      width:25%;
      //height: 60px;
      height:15%;
      margin: 30px 0;
      //font-size: 20px;
      font-size:3vw;
    }
    table {
      //position: absolute;
      //@include center();
      top: 150px;
      //top:15%;
      //width: 60%;
      width:100%;
      font-size:2vw;
      tr th:hover {
		-webkit-animation: warning 1.5s infinite ease-in;
		-moz-animation: warning 1.5s infinite ease-in;
      }
      tr {
        height: 45px;
        //height:29%;
        th {
          font-weight: normal;
          cursor: pointer;
          &:first-of-type {
            //width: 40%;
            width:20%;
            //min-width: 40px;
          }
          &:nth-of-type(2) {
            //width: 30%;
            width:20%;
            //min-width: 200px;
          }
          &:last-of-type {
            //width: 30%;
            width:20%;
            //min-width: 100px;
            cursor: default;
          }
        }
        td {
			text-align: center;
        }
        td:first-of-type {
          color: $green1;
          cursor: pointer;
        }
        td:nth-of-type(2), td:nth-of-type(3) {
          text-align: center;
        }
        td i {
          color: $green2;
          cursor: pointer;
        }
        td i:after {
          display: inline-block;
          width: 10px;
          content: '';
        }
      }
    }
    @keyframes warning {
		0% {
			text-shadow: 0px 0px 4px #000000;
		}
		50% {
			text-shadow: 0 0 40px #000000;
		}
		100% {
			text-shadow: 0 0 4px #000000;
		}
	}
	@media screen and (max-width: 500px) {
		table {
			//left:0%;
			left:40%;
			top:15%;
			float:right;
			//width:200px;
		}
		button {
			height:5%;
		}
		table tr {
			height: 35px;
		}
	}
	@media screen and (max-width: 500px) and (-webkit-min-device-pixel-ratio:2){/* 兼容iphone4/4s */
		table {
			left:20%;
		}
	}
  }

</style>



